.link {
  font-family: Ubuntu Mono;
  white-space: nowrap;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  display: flex !important;
  align-items: center;
  color: #000000 !important;
}

.logoText {
  font-family: Ubuntu Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 75%;
  display: flex;
  align-items: center;
  color: #000000;
  text-align: left;
}

nav {
  background-color: #fff;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  color: #888;
  display: flex !important;
  justify-content: space-between;
  overflow: hidden;
  height: 32px;
  // padding-left: 8px;
  padding-right: 4px;
}

nav ul {
  display: flex;
  height: auto;
  margin: 0;
  padding: 0;
}

nav ul li {
  display: flex;
  // margin: 0 12px;
  margin-right: 24px;
  list-style-type: none;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s; 
}

nav ul li a {
  border-bottom: 2px solid #FFFFFF;
}

nav > ul > li > .link:hover,
.active-link {
  border-bottom: 2px solid #1AA5B8 !important;
}

.logo:hover {
  border: none !important;
}
      
nav > div > ul > li > a > .caret {
  display: inline-block;
  margin-left: 5px;
  margin-bottom: 5px;
  width: 8px;
  height: 8px;
  border-top: 2px solid #000000;
  border-left: 2px solid #000000;
  transform: rotate(-135deg);
}

nav > div > ul > li > a > .caret:before {
  display: block;
  height: 200%;
  width: 200%;
  margin-left: -50%;
  margin-top: -50%;
  content: "";
  transform: rotate(45deg);
}

nav > div > ul > li > a,
nav > ul > li > a {
  color: #aaa;
  display: block;
  // padding: 0 24px;
  text-decoration: none !important;
  @media screen and (max-width: 1300px){
    padding: 0 10px;
  }
}
      
nav > div > ul > li > div {
  background-color: rgb( 255, 255, 255 );
  border-top: 0;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.055);
  display: none;
  margin: 0;
  opacity: 0;
  position: absolute;
  width: 200px;
  visibility: hidden;
}

nav > div > ul > li:hover > div {
  margin-top: 32px;
  display: block;
  opacity: 1;
  visibility: visible;
}

nav > div > ul > li > div ul > li {
  display: block;
  margin: 0 !important;
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

nav > div > ul > li > div ul > li > a {
  color: #fff;
  display: block;
  padding: 4px 6px;
  text-decoration: none;
  line-height: 16px !important;
}

.balans {
  display: flex;
  height: 100%;
  align-items: center;
  ul {
    margin-left: 12px;
    li {
      margin-right: 12px;
    }
  }
}

.headerText {
  display: flex;
  align-items: center;
  font-family: Ubuntu Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  text-align: right;
  color: rgba(0, 0, 0, 0.4);
  margin: 0 3px;
}

.linkBorder {
  border-bottom: none !important;
}

.popup-header {
  height: 24px;
  padding: 4px 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px rgba(0, 0, 0, 0.1) solid;
  span {
    font-family: "Ubuntu Mono", monospace;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.4);
  }
  svg {
    cursor: pointer;
  }
}

.notifications-container {
  max-width: 320px;
  display: flex;
  flex-direction: column;
  &__item {
    display: flex;
    flex-direction: row;
    align-items: start;
    padding: 4px;
    .notification-text {
      padding: 0 4px;
      line-height: 16px;
      font-family: "Ubuntu Mono", monospace;
      font-size: 14px;
      color: black;
      flex-grow: 1;
    }
    svg {
      cursor: pointer;
      width: 16px;
      height: 16px;
      min-width: 16px;
      min-height: 16px;
    }
  }
}

.bell {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 2px;
  cursor: pointer;
  &.bell-active {
    cursor: pointer;
    background: linear-gradient(0deg, rgba(26, 165, 184, 0.2), rgba(26, 165, 184, 0.2)), #FFFFFF;
    mix-blend-mode: normal;
    box-shadow: inset 0px 1px 4px rgba(0, 0, 0, 0.25);
  }
  .orange-dot {
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #F85D1F;
    left: 4px;
    top: 5px
  }
}

